.singlePost{

    flex: 9;
}

.singlePostWrapper{

    padding: 20px;
    padding-right:0 ;
}

.singlePostImg{

    width: 100%;
    height: 300px;
    border-radius: 5px;
    object-fit: cover;
}

.singlePostTitle{

    text-align: center;
    margin: 10px;
    font-size: 28px;
    font-family: 'Lora', serif;

}

.singlePostEdit{

    float: right;
    font-size:16px ;

}


.singlePostIcon{

    cursor: pointer;
    margin-left: 10px;
}

.singlePostIcon:first-child{

    color: teal;

}
.singlePostIcon:last-child{

    color: red;
}

.singlePostInfo{

    margin-bottom:20px ;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: black;
    font-family: 'Valera Round' , sans-serif;
}

.singlePostDescription{

    color: black;
    font-size: 18px;
    line-height: 25px;



}

.singlePostDescription::first-letter{


    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}




