.post{

    width: 385px;
    margin: 0px 25px 40px 25px;
}

.postImg{

    width: 80%;
    height: 200px;
    object-fit: cover;
    border-radius: 7px;
}

.postInfo{


    display: flex;
    flex-direction: column;
    align-items: center;

}

.postArt{

    font-family: 'Varela Round', sans-serif;
    color: #be9656;
    font-size: 20px;
    line-height: 20px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}


.postTitle{

    font-family: 'Josefin Sans', sans-serif;
    font-size:  24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}

.postDate{

    font-family: 'Lora', serif;
    font-style: italic;
    font-size: 13px;
    color: #999;
    margin-top: 15px;
}


.postDesciption{

    font-family: 'Varela Round', sans-serif;
    font-size: 14px;
    color: #444;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    
    -webkit-box-orient: vertical;
}