.top{

    width: 100%;
    height: 100px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
    z-index: 999;
}


.topCentre{

    flex: 6;
}
.topLeft, .topRight{

    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
   

}


.topIcon{

    font: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;

}

.topImage{

width: 40px;
height: 40px;
border-radius: 50%;
object-fit: cover;

}


.topList{


    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}



.topListItem{

    font-size: 18;
    margin-right: 20px;
    font-weight: 300;
    cursor: pointer;
}

.topSearch{
 font-size: 18;
 color: #666;
 cursor: pointer;
 margin-left: 15px;

}